.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
}

.nav__link {
  color: #060b35;
  align-items: center;
  padding: 23px 0;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

* {
  box-sizing: border-box;
}

p {
  margin: 0;
}

.card {
  color: #060b35;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-size: 14px;
  transition: all .3s;
}

.card__title {
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__imac {
  width: 160px;
  height: 134px;
}

.card__img {
  justify-content: center;
  display: flex;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.card__rating--reviews {
  font-size: 10px;
  line-height: 14px;
}

.card__sell {
  justify-content: space-between;
  padding-top: 24px;
  display: flex;
}

.card .sell--price {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card .sell--cost {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card .button-buy {
  text-transform: uppercase;
  color: #fff;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button-buy {
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  padding: 0;
}

.card__button-buy:hover {
  cursor: pointer;
  background-color: #fff;
}

.card__button-buy:hover .button-buy {
  color: #00acdc;
}

.main {
  grid-template-columns: repeat(auto-fit, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 0 72px;
  display: grid;
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

